<script>
// import VueAMap from 'vue-amap'
export default {
  props:['address'],
  created(){
    this.getLnglat()
  },
  methods:{
    getLnglat(){

      let address = this.address
      let markers = this.markers
      let center = this.center
      // let amapManager = new VueAMap.AMapManager()

      AMap.service('AMap.Geocoder',function(){//å›žè°ƒå‡½æ•°
      //å®žä¾‹åŒ–Geocoder
        let geocoder = new AMap.Geocoder({
          city: "021"//åŸŽå¸‚ï¼Œé»˜è®¤ï¼šâ€œå…¨å›½â€
        })
      //TODO: ä½¿ç”¨geocoder å¯¹è±¡å®Œæˆç›¸å…³åŠŸèƒ½
            geocoder.getLocation(address, function(status, result) {
          if (status === 'complete' && result.info === 'OK') {
            console.log(result)
              //TODO:èŽ·å¾—äº†æœ‰æ•ˆç»çº¬åº¦ï¼Œå¯ä»¥åšä¸€äº›å±•ç¤ºå·¥ä½œ
              //æ¯”å¦‚åœ¨èŽ·å¾—çš„ç»çº¬åº¦ä¸Šæ‰“ä¸Šä¸€ä¸ªMarker
            let lat =  result.geocodes[0].location.lat
            let lng =  result.geocodes[0].location.lng

            center = [lng,lat]
            
              markers.push({
                position:[lng,lat],
                content:`ðŸ·`
              })
             
          }else{
              //èŽ·å–ç»çº¬åº¦å¤±è´¥
              console.log('failed')
          }
          })
      })
      
    }
  },
  data () {
    let self = this
    return {
        zoom: 15,
        center: [121.5273285, 31.21515044],
        lng: 0,
        lat: 0,
        loaded: false,
        markers: [],
        
        plugin: [
          {
            pName: 'ToolBar',
            events: {
              init(instance) {
              }
            }
          },
          {
          pName: 'Geolocation',
          events: {
            // init(o) {
            //   // o æ˜¯é«˜å¾·åœ°å›¾å®šä½æ’ä»¶å®žä¾‹
            //   o.getCurrentPosition((status, result) => {
            //     if (result && result.position) {
            //       self.lng = result.position.lng;
            //       self.lat = result.position.lat;
            //       self.center = [self.lng, self.lat];
            //       self.loaded = true;
            //       self.$nextTick();
            //     }
            //   });
            // }
          }
        }]
    }
  },
  components : {

  }
}
</script>

<style lang="stylus" scoped>
.mapwrapper
  width 100%
  height 100%

</style>    




